import { jsPDF } from "jspdf";
import html2canvas from "html2canvas";

/**
 * Creates a styled div element for PDF generation with optimized structure
 * @param {Array} roadmapData - The roadmap data to render
 * @returns {HTMLElement} - The styled div element
 */
const createDownloadDiv = (roadmapData) => {
  const downloadDiv = document.createElement("div");
  downloadDiv.className = "roadmap-download-content";

  // Optimized base styles - applied once
  const baseStyles = {
    padding: "40px",
    color: "#2c3e50",
    backgroundColor: "white",
    fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
    fontSize: "14px",
    lineHeight: "1.6",
    maxWidth: "800px",
    margin: "0 auto",
    position: "absolute",
    left: "-9999px", // Hide during creation
    top: "0"
  };

  Object.assign(downloadDiv.style, baseStyles);

  // Add title
  const title = document.createElement("h1");
  Object.assign(title.style, {
    textAlign: "center",
    marginBottom: "30px",
    fontSize: "32px",
    fontWeight: "700",
    color: "#2c3e50",
    borderBottom: "3px solid #3498db",
    paddingBottom: "15px"
  });
  title.textContent = "AI/ML Engineer Roadmap";
  downloadDiv.appendChild(title);

  // Create all sections at once using DocumentFragment for better performance
  const fragment = document.createDocumentFragment();
  roadmapData.forEach((section) => {
    fragment.appendChild(createSectionDiv(section));
  });
  downloadDiv.appendChild(fragment);

  return downloadDiv;
};

/**
 * Creates a section element with optimized DOM creation
 * @param {Object} section - Section data
 * @returns {HTMLElement} - The section element
 */
const createSectionDiv = (section) => {
  const sectionDiv = document.createElement("div");
  Object.assign(sectionDiv.style, {
    marginBottom: "40px",
    pageBreakInside: "avoid"
  });

  // Create all elements in memory first
  const elements = [
    createSectionHeader(section),
    createSectionDescription(section.description),
    createContentSection("âœ… What to Learn", section.content.whatToLearn, "#27ae60", "list"),
    createCoursesSection(section.content.bestCourses),
    createContentSection("ðŸ§° Tools to Use", section.content.toolsToUse, "#f39c12", "list"),
    createContentSection("ðŸ“˜ Docs & Websites", section.content.docsAndWebsites, "#e74c3c", "list"),
    createContentSection("ðŸ’¡ Project Ideas", section.content.projectIdeas, "#9b59b6", "list")
  ];

  // Append all at once
  elements.forEach(el => sectionDiv.appendChild(el));
  return sectionDiv;
};

/**
 * Creates section header with optimized styling
 */
const createSectionHeader = (section) => {
  const header = document.createElement("h2");
  Object.assign(header.style, {
    backgroundColor: "#ecf0f1",
    padding: "15px 20px",
    borderRadius: "8px",
    borderLeft: "5px solid #3498db",
    fontSize: "20px",
    fontWeight: "600",
    color: "#2c3e50",
    marginBottom: "20px"
  });
  header.textContent = `${section.id}. ${section.title}`;
  return header;
};

/**
 * Creates section description
 */
const createSectionDescription = (description) => {
  const desc = document.createElement("p");
  Object.assign(desc.style, {
    marginBottom: "25px",
    fontStyle: "italic",
    fontSize: "15px",
    color: "#7f8c8d",
    lineHeight: "1.7",
    padding: "0 10px"
  });
  desc.textContent = description;
  return desc;
};

/**
 * Generic content section creator - reduces code duplication
 */
const createContentSection = (titleText, items, color, type = "list") => {
  const containerDiv = document.createElement("div");
  containerDiv.style.marginBottom = "25px";

  const title = document.createElement("h3");
  Object.assign(title.style, {
    fontSize: "18px",
    fontWeight: "600",
    color: color,
    marginBottom: "12px",
    borderBottom: `2px solid ${color}`,
    paddingBottom: "5px"
  });
  title.textContent = titleText;
  containerDiv.appendChild(title);

  if (type === "list") {
    const list = document.createElement("ul");
    Object.assign(list.style, {
      paddingLeft: "25px",
      margin: "15px 0"
    });

    // Create all list items at once
    const listItems = items.map(item => {
      const li = document.createElement("li");
      Object.assign(li.style, {
        marginBottom: "8px",
        fontSize: "14px",
        lineHeight: "1.6",
        color: "#34495e"
      });
      li.textContent = item;
      return li;
    });

    listItems.forEach(li => list.appendChild(li));
    containerDiv.appendChild(list);
  }

  return containerDiv;
};

/**
 * Creates the "Best Courses" section with optimized structure
 */
const createCoursesSection = (bestCourses) => {
  const bestCoursesDiv = document.createElement("div");
  bestCoursesDiv.style.marginBottom = "25px";

  const title = document.createElement("h3");
  Object.assign(title.style, {
    fontSize: "18px",
    fontWeight: "600",
    color: "#3498db",
    marginBottom: "12px",
    borderBottom: "2px solid #3498db",
    paddingBottom: "5px"
  });
  title.textContent = "ðŸ“š Best Courses";
  bestCoursesDiv.appendChild(title);

  // Create both language sections
  ['english', 'hindi'].forEach(lang => {
    const langTitle = document.createElement("h4");
    Object.assign(langTitle.style, {
      fontSize: "16px",
      fontWeight: "500",
      color: "#2c3e50",
      marginTop: "15px",
      marginBottom: "10px"
    });
    langTitle.textContent = `In ${lang.charAt(0).toUpperCase() + lang.slice(1)}:`;
    bestCoursesDiv.appendChild(langTitle);

    const list = document.createElement("ul");
    Object.assign(list.style, {
      paddingLeft: "25px",
      margin: "10px 0"
    });

    bestCourses[lang].forEach(course => {
      const li = document.createElement("li");
      Object.assign(li.style, {
        marginBottom: "6px",
        fontSize: "13px",
        lineHeight: "1.5",
        color: "#34495e"
      });
      li.textContent = course;
      list.appendChild(li);
    });
    bestCoursesDiv.appendChild(list);
  });

  return bestCoursesDiv;
};

/**
 * Optimized PDF generation with performance improvements
 * @param {Array} roadmapData - The complete roadmap data
 * @param {Function} setDownloading - State setter for download status
 */
export const downloadRoadmapPDF = async (roadmapData, setDownloading) => {
  setDownloading(true);

  try {
    // Create and append div immediately (hidden)
    const downloadDiv = createDownloadDiv(roadmapData);
    document.body.appendChild(downloadDiv);

    // Use requestAnimationFrame for better performance
    await new Promise(resolve => requestAnimationFrame(resolve));

    // Optimized html2canvas settings for speed
    const canvas = await html2canvas(downloadDiv, {
      scale: 1.2, // Slightly reduced for speed
      useCORS: true,
      logging: false,
      letterRendering: true,
      allowTaint: false,
      backgroundColor: "#ffffff",
      width: downloadDiv.scrollWidth,
      height: downloadDiv.scrollHeight,
      scrollX: 0,
      scrollY: 0,
      foreignObjectRendering: false, // Disable for better performance
      imageTimeout: 5000, // Faster timeout
      removeContainer: false // We'll handle cleanup manually
    });

    // Remove DOM element immediately after canvas creation
    document.body.removeChild(downloadDiv);

    // Create PDF with compression enabled
    const pdf = new jsPDF({
      orientation: "portrait",
      unit: "mm",
      format: "a4",
      compress: true,
      precision: 16 // Higher precision for better quality
    });

    // Optimized image processing
    const imgWidth = 210;
    const imgHeight = (canvas.height * imgWidth) / canvas.width;
    const pageHeight = 297;

    // Convert canvas to optimized image data once
    const imgData = canvas.toDataURL("image/jpeg", 0.85); // Balanced quality/size

    let heightLeft = imgHeight;
    let position = 0;

    // Add first page
    pdf.addImage(imgData, "JPEG", 0, 0, imgWidth, imgHeight);
    heightLeft -= pageHeight;

    // Add additional pages efficiently
    while (heightLeft > 0) {
      position = heightLeft - imgHeight;
      pdf.addPage();
      pdf.addImage(imgData, "JPEG", 0, position, imgWidth, imgHeight);
      heightLeft -= pageHeight;
    }

    // Generate and save PDF
    const fileName = `AI_ML_Engineer_Roadmap_${Date.now()}.pdf`;
    pdf.save(fileName);

  } catch (error) {
    console.error("Error generating PDF:", error);

    // More specific error messages
    const errorMsg = error.message.includes('canvas') 
      ? "Failed to render content. Please try again." 
      : "Failed to generate PDF. Please check your browser settings and try again.";

    alert(errorMsg);
  } finally {
    setDownloading(false);
  }
};

/**
 * Alternative faster PDF generation using jsPDF's built-in text methods
 * This method is significantly faster but with simpler formatting
 * @param {Array} roadmapData - The complete roadmap data
 * @param {Function} setDownloading - State setter for download status
 */
export const downloadRoadmapPDFTextOnly = async (roadmapData, setDownloading) => {
  setDownloading(true);

  try {
    const pdf = new jsPDF({
      orientation: "portrait",
      unit: "mm",
      format: "a4"
    });

    const pageWidth = pdf.internal.pageSize.getWidth();
    const pageHeight = pdf.internal.pageSize.getHeight();
    const margin = 20;
    const lineHeight = 7;
    let yPosition = margin;

    // Add title
    pdf.setFontSize(24);
    pdf.setFont(undefined, 'bold');
    pdf.text("AI/ML Engineer Roadmap", pageWidth / 2, yPosition, { align: 'center' });
    yPosition += lineHeight * 2;

    // Process each section
    roadmapData.forEach(section => {
      // Check if we need a new page
      if (yPosition > pageHeight - 40) {
        pdf.addPage();
        yPosition = margin;
      }

      // Section title
      pdf.setFontSize(18);
      pdf.setFont(undefined, 'bold');
      pdf.text(`${section.id}. ${section.title}`, margin, yPosition);
      yPosition += lineHeight * 1.5;

      // Section description
      pdf.setFontSize(12);
      pdf.setFont(undefined, 'italic');
      const descLines = pdf.splitTextToSize(section.description, pageWidth - 2 * margin);
      pdf.text(descLines, margin, yPosition);
      yPosition += descLines.length * lineHeight + 5;

      // Process each content type
      const contentSections = [
        { title: "What to Learn", items: section.content.whatToLearn },
        { title: "Tools to Use", items: section.content.toolsToUse },
        { title: "Docs & Websites", items: section.content.docsAndWebsites },
        { title: "Project Ideas", items: section.content.projectIdeas }
      ];

      contentSections.forEach(contentSection => {
        if (yPosition > pageHeight - 30) {
          pdf.addPage();
          yPosition = margin;
        }

        pdf.setFontSize(14);
        pdf.setFont(undefined, 'bold');
        pdf.text(contentSection.title, margin, yPosition);
        yPosition += lineHeight;

        pdf.setFontSize(11);
        pdf.setFont(undefined, 'normal');
        contentSection.items.forEach(item => {
          if (yPosition > pageHeight - 20) {
            pdf.addPage();
            yPosition = margin;
          }
          const itemLines = pdf.splitTextToSize(`â€¢ ${item}`, pageWidth - 2 * margin - 10);
          pdf.text(itemLines, margin + 5, yPosition);
          yPosition += itemLines.length * lineHeight;
        });
        yPosition += 5;
      });

      // Best Courses section (special handling)
      if (yPosition > pageHeight - 40) {
        pdf.addPage();
        yPosition = margin;
      }

      pdf.setFontSize(14);
      pdf.setFont(undefined, 'bold');
      pdf.text("Best Courses", margin, yPosition);
      yPosition += lineHeight;

      ['english', 'hindi'].forEach(lang => {
        pdf.setFontSize(12);
        pdf.setFont(undefined, 'bold');
        pdf.text(`In ${lang.charAt(0).toUpperCase() + lang.slice(1)}:`, margin + 5, yPosition);
        yPosition += lineHeight;

        pdf.setFontSize(11);
        pdf.setFont(undefined, 'normal');
        section.content.bestCourses[lang].forEach(course => {
          if (yPosition > pageHeight - 20) {
            pdf.addPage();
            yPosition = margin;
          }
          const courseLines = pdf.splitTextToSize(`â€¢ ${course}`, pageWidth - 2 * margin - 15);
          pdf.text(courseLines, margin + 10, yPosition);
          yPosition += courseLines.length * lineHeight;
        });
        yPosition += 3;
      });

      yPosition += 10; // Space between sections
    });

    // Save the PDF
    const fileName = `AI_ML_Engineer_Roadmap_${Date.now()}.pdf`;
    pdf.save(fileName);

  } catch (error) {
    console.error("Error generating text-only PDF:", error);
    alert("Failed to generate PDF. Please try again.");
  } finally {
    setDownloading(false);
  }
};